export default {
	props:{
		item: Object,
		index: Number,
		btn:Array
	},
	data() {
		return {
			data:[]
		}
	},
	onReady() {
		console.log("要传递的值");
		console.log(this.item);
		
		this.data.push(this.item);
		console.log(this.data);
	},
	methods:{
		openpage(index){
			console.log(this.data[index]);
			switch (index){
				case 0:
					uni.navigateTo({
						url:'../../pages/order-detail/order-detail?data='+this.data[index]
					})
					break;
				default:
					break;
			}
		}
	}
}


